import './App.css';
import {Tabs, Tab, Card, CardBody, Switch } from "@nextui-org/react";
import React, {useState} from 'react';
import Blocks from "./Pages/Blocks";
import Wallets from "./Pages/Wallets";
import Pending from "./Pages/Pending";
import Transactions from "./Pages/Transactions";
import Nodes from "./Pages/Nodes";
import Settings from "./Pages/Settings"; 
import {SunIcon} from "./Icons/SunIcon.jsx"
import {MoonIcon} from "./Icons/MoonIcon.jsx"

// Importe les modules ncessaires pour la blockchain, les transactions et la gnration de cls
const Blockchain = require('./Data/blockchain')
const Transaction = require('./Data/transaction')
const { KeyGenerator } = require('./Data/signature')

// Cre une instance de la blockchain pour l'exemple
export const exchain = new Blockchain()

// Gnre une adresse publique pour un mineur en utilisant KeyGenerator et l'exporte
export const minorAddress = new KeyGenerator().generate().publicKey

// Initialise et exporte une liste vide pour stocker les portefeuilles
export const wallets = []

// Dfinit et exporte une fonction pour crer un nouveau portefeuille
export const newWallet = (name, balance = 0) => {
  // Gnre une nouvelle paire de cls pour le portefeuille
  let wallet = (new KeyGenerator()).generate()
  // Cre une transaction initiale pour crditer le portefeuille avec 1000 units
  // en utilisant la cl prive du systme pour signer la transaction
  new Transaction(exchain.system.publicKey, wallet.publicKey, 1000).sign(exchain.system.privateKey)
  // Mine un nouveau bloc pour inclure la transaction prcdente
  exchain.mine(minorAddress)
  // Ajoute le nouveau portefeuille  la liste des portefeuilles avec son nom, cls et solde
  wallets.push({
    name: name,
    privateKey: wallet.privateKey,
    publicKey: wallet.publicKey,
    balance: exchain.getBalance(wallet.publicKey) // Rcupre le solde actuel du portefeuille
  })
}

// Cre un exemple de portefeuille nomm 'Mon Wallet'
newWallet('Mon Wallet');

// Cre une transaction systme pour crditer le premier portefeuille de la liste avec 1000 units
const systemTransaction = (new Transaction(exchain.system.publicKey, wallets[0].publicKey, 1000)).sign(exchain.system.privateKey)
// Met  jour le solde du premier portefeuille dans la liste aprs la transaction
wallets[0].balance = exchain.getBalance(wallets[0].publicKey)
// Ajoute la transaction systme  la liste des transactions en attente de la blockchain
exchain.addTransaction(systemTransaction)




function App() {
  const [isDark, setIsDark] = useState(true)
  const [theme, setTheme] = useState("dark")
  return (
    <body className= {theme +" text-foreground bg-background min-h-screen"}>
    <Switch
      size="lg"
      color="secondary"
      startContent={<SunIcon />}
      endContent={<MoonIcon />}
      onChange={() =>{console.log(isDark);setTheme(isDark ? "light" : "dark"); setIsDark(!isDark)}}
    >
      Dark mode
    </Switch>
      <Tabs className="flex justify-center h-full" aria-label="Options">
        <Tab title="Blocks">
          <Card>
            <CardBody>
              <Blocks />
            </CardBody>
          </Card>
        </Tab>
        <Tab title="Wallets">
          <Card>
            <CardBody>
              <Wallets />
            </CardBody>
          </Card>
        </Tab>
        <Tab title="En attente">
          <Card>
            <CardBody>
              <Pending />
            </CardBody>
          </Card>
        </Tab>
        <Tab title="Transaction">
          <Card>
            <CardBody>
              <Transactions />
            </CardBody>
          </Card>
        </Tab>
        <Tab title="Nodes">
          <Card>
            <CardBody>
              <Nodes />
            </CardBody>
          </Card>
        </Tab>
        <Tab title="Parametres">
          <Card>
            <CardBody>
              <Settings />
            </CardBody>
          </Card>
        </Tab>
      </Tabs>
    </body>
  );
}

export default App;
